.App {
  min-height: 100vh;
  min-width: 100vw;
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600;700&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
*::selection {
    background: #ee0a21;
    color: #fff;
}
body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #fc2732;
}
.wrapper {
    position: relative;
    width: 600px;
    height: 400px;
    background-color: #ff9393;
    backdrop-filter: blur(5px);
    box-shadow: 0px 4px 25px black;
    box-shadow: black 20px;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

h2 {
    font-size: 3em;
    color: #7d2ae8;
    margin: 15px 0;
}
.btn-group {
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: center;
    margin-top: 15px;
}
button {
    position: absolute;
    width: 150px;
    height: inherit;
    font-size: 1.2em;
    color: #fff;
    font-weight: 600;
    border-radius: 30px;
    border: 2px solid #fc2732;
    outline: none;
    cursor: pointer;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .3);
}
button:nth-child(1) {
    margin-left: -200px;
    background: #fc2732;
}
button:nth-child(2) {
  cursor: not-allowed;
  transition: 0.2s;
    margin-right: -200px;
    color: #fc2732;
}

.main{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.img1{
    width: 200px;
    height: 200px;
    border-radius: 50%;
}